@import url(https://fonts.googleapis.com/css2?family=Anton&family=Yanone+Kaffeesatz:wght@200;300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Anton&family=Yanone+Kaffeesatz:wght@200;300;400&display=swap);
@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.4.0/font/bootstrap-icons.css);
@import url(https://fonts.googleapis.com/css2?family=Anton&family=Yanone+Kaffeesatz:wght@200;300;400&display=swap);
* {
  font-family: 'Yanone Kaffeesatz', sans-serif;
}

body {
  margin: 0;
  font-family: 'Yanone Kaffeesatz', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* * {
  font-family: 'Encode Sans', sans-serif;
  
} */

.jumbotron {
  text-align: center;
  background: rgba(31, 99, 52, 0.653);
  color: #ffffff;
  border-bottom: 10px solid black;
  min-height: 30vh;
  min-width: 100%;
  -webkit-justify-content: center;
          justify-content: center;
 
}

h1 {
  padding-top: 10px;
  font-family: 'Anton', sans-serif;
  text-shadow: 4px 4px 4px rgb(10, 10, 10),
  1px 1px 2px rgb(11, 8, 8),
   1px -1px 1px #000,
 1px -1px 1px rgb(0, 0, 0);
}

h2 {
  font-size: 2em;
  font-weight: bold;
  text-shadow: 2px 2px 2px rgb(10, 10, 10),
  -1px -1px 1px rgb(0, 0, 0);
}

h3 {
  font-size: 1.5em;
  text-shadow: 1px 1px 1px rgb(10, 10, 10);
}

h2, 
h3 {
  font-family: 'Yanone Kaffeesatz', sans-serif;
}



form {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 10px;
    gap: 10px;
    padding: 5px;
    font-family: 'Yanone Kaffeesatz', sans-serif;
    font-size: 1.3em;
}

.searchBtn {
    border-radius: 7px;
    min-width: 50%;
    background: rgb(205, 205, 255);
    box-shadow: 4px 4px 4px black;
}

.searchBtn:hover {
    font-size: 1.5em;
    background-color: rgb(126, 107, 184);
    transition: all .3s ease-in-out;
}

input {
    border: 1px solid;
    border-radius: 7px;
    box-shadow: 4px 4px 4px black;
    padding: 0.5em;
    min-width: 50%;
}
* {
  font-family: 'Yanone Kaffeesatz', sans-serif;
}

.spacing {
   padding: 10px;
}

.datatable {
  font-family: 'Yanone Kaffeesatz', sans-serif;
}

.empTable {
  /* font-family: 'Yanone Kaffeesatz', sans-serif; */
  font-size: 1.3em;
  border-collapse: collapse;
  width: 100%;
  -webkit-align-items: center;
          align-items: center;
  
}

#empTable td,
#empTable th {
  border: 1px solid #ddd;
  padding: 15px;
  /* align-content: center; */
}

#empTable tr:nth-child(even) {
  background-color: #f2f2f2;
}

#empTable tr:hover {
  background-color: rgb(235, 205, 205);
}

thead {
  /* display: table-header-group; */
  font-family: 'Yanone Kaffeesatz', sans-serif;
  padding-top: 12px;
  padding-bottom: 20px;
  background: rgba(31, 99, 52, 0.653);
  color: rgb(4, 1, 1);
  -webkit-justify-content: space-between;
          justify-content: space-between;
  text-align: center;
  padding-bottom: 18px;
  text-transform: uppercase;
  border: solid 2px black;
  border-radius: 5px;
}

th{
  border: solid 2px black;
  color: white;
  font-family: bold;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px;
  text-shadow: 
  1px 1px 1px rgb(10, 10, 10),
  -1px 1px 2px rgb(0, 0, 0),
   1px -1px 1px #000,
 -1px -1px 1px rgb(0, 0, 0);
 
}

td {
  text-align: center;
  padding-top: 10px;
}

tr {
padding: 10px;
}

img {
  border: solid .5px black;
  border-radius: 0.3em;
}

a {
  color: rgb(84, 145, 84);
}

i {
  padding-left: 5px;
  font-weight: bold;
}
